.todo-list-item {
    font-size: 1.25rem;
  }
  
  .todo-list-item button {
    width: 57px;
    margin: 3px;
  }
  
  .todo-list-item-label {
    margin-left: 1.25rem;
    line-height: 35px;
    cursor: pointer;
    user-select: none;
  }
  
  .todo-list-item.done .todo-list-item-label {
    color:aqua;
  }
